
body, h1, h2, h3, h4, h5, .btn, .job-meta, .app-jumbotron, p.lead {

}

p, ul, li  {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 18px;
}

.app {
    line-height: 1.7;
}

.app-content {
    padding: 1em;
}

.app-jumbotron {
    /* background: url('https://images.unsplash.com/photo-1460794418188-1bb7dba2720d?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=d2bfe8be765e57893fa314522a14050c&auto=format&fit=crop&w=1950&q=80') no-repeat;
    background-size: cover; */
}

.card {
    margin-bottom: 1em;
}

.app-search-input {
    width: 360px !important;
}

.job-list-item {
    cursor: pointer;
    padding: 1em;
    border-radius: 5px;
    transition: background 0.12s ease-out;
}


.job-list-item:hover .job-title {
    color: blue;
}

.job-title {
    margin: 0;
    padding: 0;
    font-size: 1.2em;
    font-weight: bold;
}
.job-title-expanded {
    font-size: 2em;
    padding-top: 1em;
}

.job-sidebar {
    padding-top: 1em;
}


.job-apply-button {
    background: #0087E0;
    color: white;
    padding: 0.5em 2em;
    border-radius: 5px;
    text-decoration: none;
    transition: all 0.3s ease-out;
    font-weight: bold;
    font-size: 1.2em;
}
.job-apply-button:hover {
    background: #36AAEB;
}

.job-description ul {
    margin: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.job-description br {
    display: none;
}

.job-description h1,
.job-description h2,
.job-description h3,
.job-description h4 {
    font-size: 1.3em;
}



.muted {
    color: #6c718a;
    font-weight: 100;
}



.paginate ul {
    display: inline-block;
}

.paginate li {
    display: inline-block;
    padding: 0.5em;
}

.paginate .break a {
    cursor: default;
}

.footer {
    background: url('data:image/svg+xml;base64,PHN2ZyBwcmVzZXJ2ZUFzcGVjdFJhdGlvPSJub25lIiB2aWV3Qâ€¦kiPjwvcGF0aD4KICAgICAgICAgICAgPC9nPgogICAgICAgIDwvZz4KICAgIDwvZz4KPC9zdmc+') no-repeat;
    background-size: cover;
    height: 300px;
}


